import PropTypes from 'prop-types';

import React from 'react';

import {Modal} from 'react-bootstrap';
import {getFileUrl} from 'mattermost-redux/utils/file_utils';

import {
    clearFileInput,
    generateId,
    getTimestamp
} from 'utils/utils.jsx';
import LoadingSpinner from 'components/widgets/loading/loading_spinner';

export default class UpdateChannelAvatarModal extends React.PureComponent {
    static propTypes = {

        /*
         * callback to call when modal will hide
          */
        onHide: PropTypes.func.isRequired,

        /**
         *  Data used for posting message
         */
        currentUserId: PropTypes.string,

        /*
         * Channel info object
         */
        channel: PropTypes.object,

        /*
         * Object with redux action creators
         */
        actions: PropTypes.shape({

            /*
             * Action creator to patch current channel
             */
            onSubmitPost: PropTypes.func.isRequired,
            scrollPostListToBottom: PropTypes.func.isRequired,
            patchChannel: PropTypes.func.isRequired,
            uploadFile: PropTypes.func.isRequired,
            handleFileUploadEnd: PropTypes.func.isRequired
        }).isRequired,
    }

    constructor(props) {
        super(props);

        this.state = {
            show: true,
            error: null
        };

        this.fileInput = React.createRef();
    }

    onHide = () => {
        this.setState({show: false});
    }

    getFileUploadTarget = () => {
        if (this.refs.textbox) {
            return this.refs.textbox.getWrappedInstance();
        }

        return null;
    }

    handleChange = (e) => {
        if (e.target.files.length > 0) {
            this.uploadFile(e.target.files[0]);
            clearFileInput(e.target);
        } else {
            this.onHide();
        }
    }

    uploadFile = (file) => {
        this.setState({error: null});

        const {channel} = this.props;

        const clientId = generateId();

        const request = this.props.actions.uploadFile(
            file,
            file.name,
            channel.id,
            '',
            clientId,
        );

        request.end(async (err, res) => {
            const {error, data} = this.props.actions.handleFileUploadEnd(
                file,
                file.name,
                channel.id,
                '',
                clientId,
                {err, res},
            );

            if (error) {
                this.setState({error});
            } else if (data) {
                const upload = data.file_infos[0];
                const avatar = getFileUrl(upload.id);

                const post = {};
                post.file_ids = [upload.id];
                post.message = 'Update Channel Avatar';

                post.channel_id = channel.id;

                const time = getTimestamp();
                const userId = this.props.currentUserId;
                post.pending_post_id = `${userId}:${time}`;
                post.user_id = userId;
                post.create_at = time;
                post.parent_id = null;
                post.metadata = {};
                post.props = {};

                const {actions: {patchChannel, onSubmitPost, scrollPostListToBottom}} = this.props;

                onSubmitPost(post, data.file_infos);
                scrollPostListToBottom();

                const patchChannelResult = await patchChannel(channel.id, {
                    avatar
                });

                if (patchChannelResult.error) {
                    this.setState({error});
                } else {
                    this.onHide();
                }
            }
        });
    }

    simulateInputClick = () => {
        this.fileInput.current.click();
    }

    render() {
        return (
            <Modal
                dialogClassName='a11y__modal'
                show={this.state.show}
                onHide={this.onHide}
                onExited={this.props.onHide}
                onEntered={this.simulateInputClick}
                role='dialog'
                aria-labelledby='editChannelPurposeModalLabel'
            >
                <Modal.Header closeButton={true}>
                    <Modal.Title
                        componentClass='h1'
                        id='editChannelPurposeModalLabel'
                    >
                        {'Update Channel Avatar'}
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <LoadingSpinner/>
                    <input
                        id='fileUploadInput'
                        tabIndex='-1'
                        ref={this.fileInput}
                        type='file'
                        onChange={this.handleChange}
                        multiple={false}
                        accept={'image/*'}
                    />
                </Modal.Body>
            </Modal>
        );
    }
}
