import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import {patchChannel} from 'mattermost-redux/actions/channels';

import {getCurrentUserId} from 'mattermost-redux/selectors/entities/users';

import {uploadFile, handleFileUploadEnd} from 'actions/file_actions.jsx';

import {createPost} from 'actions/post_actions.jsx';

import {scrollPostListToBottom} from 'actions/views/channel';

import UpdateChannelAvatarModal from './update_channel_avatar_modal.jsx';

function onSubmitPost(post, fileInfos) {
    return (dispatch) => {
        dispatch(createPost(post, fileInfos));
    };
}

function mapStateToProps(state) {
    return {
        currentUserId: getCurrentUserId(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        actions: bindActionCreators({
            onSubmitPost,
            patchChannel,
            uploadFile,
            handleFileUploadEnd,
            scrollPostListToBottom
        }, dispatch)
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(UpdateChannelAvatarModal);

